@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* Colors */
  --bg-color: #f5f5f5;
  --white: #ffffff;
  --black: #000000;
  --yellow-light: #dcc655;
  --yellow-dark: #936409;
  --blue-light: #5a7dd6;
  --blue-dark: #291a82;
  --red-light: #db2c2c;
  --red-dark: #7b0a0a;
  --green-light: #5cb69b;
  --green-dark: #175744;
  --grey-light: rgba(217, 217, 217, 0.7);
  --grey-middle: #b8b8b8;
  --grey-dark: rgba(101, 101, 101, 0.9);

  /* Font */
  --f-family: 'Inter', sans-serif;
  --fw-thin: 100;
  --fw-regular: 400;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3.125rem 0;
  font-family: var(--f-family);
  font-weight: var(--fw-regular);
  font-size: 16px;
  background-color: var(--bg-color);
}
.app {
  position: relative;
  overflow: hidden;
  width: 640px;
  min-height: 840px;
  background-color: var(--white);
  padding: 0.5rem 1.5rem 1.25rem;
}

.toolbar {
  position: absolute;
  top: 10rem;
  left: 0;
  display: flex;
  flex-direction: column;
  row-gap: 5rem;
  padding: 2rem 1rem 2rem 0.6rem;
  background-color: var(--grey-light);
  div {
    display: flex;
    flex-direction: column;
    row-gap: 0.87rem;
  }
}
